<template>
  <div class="home">
    <div class="grid">
      <div class="box" :class="rotate ? 'box-rotate': ''">
        <div class="flex-box">
            <Badge class="badge"></Badge>
            <p>从10月15号认识到现在</p>
        </div>
        <div>一直有个问题想问你</div>
        <div>但又不好意思开口</div>
        <div>所以了选择这种方式</div>
        <div>那么，可以问么？</div>
      </div>
      <div class="btn-group">
        <div :class="translate ? 'btn-left':''" @click="problem(true)">可以呀，你问吧</div>
        <div :class="translate ? 'btn-right':''" @click="problem(false)">不可以，不想听</div>
      </div>
    </div>
    <div class="modal-box" v-if="showModal" >
      <div class="modal">
        <p v-html="modalText"></p>
        <p v-html="second"></p>
      </div>
    </div>
  </div>
</template>

<script>
  import Badge from '../components/Badge.vue'

  export default {
    name: 'home',
    components: {
      Badge,
    },
    data() {
      return {
        rotate: false,
        translate: true,
        showModal: false,
        modalText: '哼，就算你不答应我也要问',
        second: '准备开始啦,倒计时 5 秒',
        
      }
    },
    mounted() {},
    created() {
      this.handleRotate()
      this.handleTanslate()
    },
    methods: {
      // 实现加载旋转动画显示
      handleRotate() {
        setTimeout(() => {
          this.rotate = true;
        }, 100);
      },
      handleTanslate() {
        setTimeout(() => {
          this.translate = false;
        }, 500);
      },
      problem(type) {
        let time = 5;
        this.showModal = true
        if(type) {
          this.modalText = '那我就开始问啦，哈哈哈哈哈哈'
        }
        // 创建计时器
        const timer = setInterval(()=> {
          time--;
          this.second = `准备开始啦,倒计时 ${time} 秒`;
          if(time==0) {
            clearInterval(timer)
            this.$router.push({
              path: '/problem'
            })
          }
        },1000)

        
      }
    },
  }
</script>
